.form__control {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form__control-label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form__fieldset {
  border: none;
  margin-bottom: 49px;

  @media (min-width: $desktop-width) {
    margin-bottom: 56px;
  }
}

.form__wrapper {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    padding-left: 60px;
    padding-right: 60px;
    padding-top: 108px;
    padding-bottom: 63px;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-around;
    padding-left: 120px;
    padding-right: 120px;
    padding-top: 68px;
    padding-bottom: 71px;
    margin: 0;
  }
}

.form__fields {
  border: none;
  padding: 0;
}

.form__controls {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: 60px;
    margin-right: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 120px;
    margin-right: 120px;
  }
}

.form__button {
  display: block;
  width: 280px;
  height: 58px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  background: #81b3d2;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  border: none;
  margin-left: 20px;

  @media (min-width: $tablet-width) {
    margin: 0;
    width: 324px;
  }

  @media (min-width: $desktop-width) {
    width: 384px;
    margin-left: 100px;
  }
}

.form__tip {
  padding-left: 163px;
  padding-right: 20px;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 0.6);

  @media (min-width: $tablet-width) {
    margin-right: 0;
    padding-right: 0;
  }

  @media (min-width: $desktop-width) {
    margin-right: 90px;
  }
}
